.line {
  position: relative;

  @media (min-width: @tablet-width) {
    height: fit-content;
  }

  &__title {
    margin: 0;
    color: var(--green);
    font-size: 17px;
    line-height: 16px;
    display: inline-block;
    position: relative;
    padding: 0;
    padding-right: 23px;
    background-color: var(--white);
    text-transform: uppercase;

    @media (min-width: @tablet-width) {
      padding: 0;

      &--gray {
        background-color: var(--gray-light-back);
      }
    }
  }

  &::before {
    content: "";
    width: 100%;
    height: 1px;
    position: absolute;
    background-color: var(--green);
    top: 50%;
    transform: translateY(-50%);

    @media (min-width: @tablet-width) {
      content: none;
    }
  }
}
