.button {
  display: block;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  color: var(--black-light);
  border: 2px solid var(--black-light);
  padding: 16px 0;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    font-size: 17px;
  }
}
