:root {
  font-size: 0.82353em;
  --green: #63D1BB;
  --green-dark: #56c3b2;
  --black: #000000;
  --black-light: #444444;
  --gray: #ECECEC;
  --gray-light: #E5E5E5;
  --gray-back: #F9F9F9;
  --gray-back-light: #F1F1F1;
  --white: #fff;
}
@media (min-width: 768px) {
  :root {
    font-size: 0.884em;
  }
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.container {
  width: auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0;
    max-width: 1050px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  color: var(--black-light);
  border: 2px solid var(--black-light);
  padding: 16px 0;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.input {
  padding: 0;
  box-sizing: border-box;
  border: none;
  padding: 17px 0;
  border-bottom: 2px solid #000000;
  font-size: 17px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .input {
    font-size: 19px;
    padding-left: 20px;
  }
}
.page-body {
  font-family: "Open Sans", Arial, sans-serif;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr auto;
  grid-template-areas: "header" "main" "footer";
}
.page-body__header {
  background-color: var(--white);
  grid-area: header;
}
.page-body__main {
  grid-area: main;
}
.page-body__footer {
  grid-area: footer;
}
.main-header {
  position: relative;
  background-color: var(--white);
  overflow: hidden;
}
@media (min-width: 768px) {
  .main-header {
    background-color: var(--gray-back);
  }
  .main-header__container {
    padding: 0;
  }
}
.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 17px;
  display: none;
}
@media (min-width: 768px) {
  .menu {
    font-size: 15px;
  }
}
.menu__item {
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .menu__item {
    border-bottom: none;
  }
}
.menu__link {
  text-align: center;
  display: block;
  text-decoration: none;
  color: var(--black);
  padding: 29px 0 27px 0;
  font-weight: bold;
}
@media (min-width: 768px) {
  .menu__link {
    text-align: start;
    padding: 0;
  }
  .menu__link--end {
    text-align: end;
  }
}
.navigation {
  position: relative;
}
.navigation__info {
  display: none;
  margin: 0;
  position: relative;
}
@media (min-width: 768px) {
  .navigation {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content 1fr;
    grid-template-areas: "logo user" "main parag";
    align-items: center;
    background-color: var(--white);
  }
  .navigation__logo-burger {
    grid-area: logo;
  }
  .navigation__main-menu {
    grid-area: main;
    align-self: flex-start;
    width: 100%;
    height: 100%;
  }
  .navigation__user-menu {
    grid-area: user;
    width: 351px;
    margin-left: auto;
  }
  .navigation__info {
    grid-area: parag;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: end;
    width: 100%;
    height: 100%;
    background-color: var(--gray-back);
    align-self: flex-start;
    padding-right: 50px;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "main logo user" "events . parag";
  }
  .navigation__events-menu {
    grid-area: events;
    align-self: flex-start;
    width: 100%;
    height: 100%;
    grid-column: 1 / 3;
  }
  .navigation__logo-burger {
    align-self: flex-start;
  }
  .navigation__main-menu {
    position: static;
    height: 100%;
  }
  .navigation__user-menu {
    align-self: flex-start;
  }
  .navigation__info {
    padding-right: 0;
  }
}
.main-menu {
  position: relative;
  box-sizing: border-box;
  background-color: var(--white);
}
@media (min-width: 768px) {
  .main-menu {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    background-color: var(--gray-back);
    padding-left: 50px;
  }
  .main-menu__item {
    flex-grow: 1;
  }
  .main-menu__link {
    padding: 20px 0;
  }
}
@media (min-width: 1150px) {
  .main-menu {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    align-self: flex-start;
    background: none;
    padding-left: 0;
  }
  .main-menu::before {
    content: "";
    position: absolute;
    top: 0;
    right: 349px;
    width: calc(50vw - 525px);
    height: 100%;
    background-color: var(--white);
  }
  .main-menu__link {
    padding: 33px 0 32px 0;
  }
  .main-menu__item {
    flex-grow: 0;
  }
}
@media (min-width: 768px) {
  .user-menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    background-color: var(--white);
    padding-right: 50px;
  }
  .user-menu__item {
    flex-grow: 1;
  }
  .user-menu__span {
    display: none;
  }
  .user-menu__icon-search {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .user-menu__link-basket {
    padding: 20px 0;
  }
  .user-menu__basket {
    margin-right: 31px;
    margin-left: 33px;
  }
  .user-menu__search {
    position: relative;
    padding: 44px 40px;
    border-left: 2px solid var(--gray);
    border-right: 2px solid var(--gray);
  }
}
@media (min-width: 1150px) {
  .user-menu {
    padding-right: 0;
  }
  .user-menu::after {
    content: "";
    position: absolute;
    top: 0;
    left: 350px;
    width: calc(50vw - 526px);
    height: 100%;
    background-color: var(--white);
  }
}
.events-menu {
  display: none;
}
@media (min-width: 1150px) {
  .events-menu {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 65px;
    background-color: var(--gray-back);
  }
