.navigation {
  position: relative;

  &__info {
    display: none;
    margin: 0;
    position: relative;
  }

  @media (min-width: @tablet-width) {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content 1fr;
    grid-template-areas:
      "logo user"
      "main parag";
    align-items: center;
    background-color: var(--white);

    &__logo-burger {
      grid-area: logo;
    }

    &__main-menu {
      grid-area: main;
      align-self: flex-start;
      width: 100%;
      height: 100%;
    }

    &__user-menu {
      grid-area: user;
      width: 351px;
      margin-left: auto;
    }

    &__info {
      grid-area: parag;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: end;
      width: 100%;
      height: 100%;
      background-color: var(--gray-back);
      align-self: flex-start;
      padding-right: 50px;
    }
  }
  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas:
      "main logo user"
      "events . parag";

    &__events-menu {
      grid-area: events;
      align-self: flex-start;
      width: 100%;
      height: 100%;
      grid-column: 1 / 3;
    }

    &__logo-burger {
      align-self: flex-start;
    }

    &__main-menu {
      position: static;
      height: 100%;
    }

    &__user-menu {
      align-self: flex-start;
    }

    &__info {
      padding-right: 0;
    }
  }
}
